			},
			getSearchV: function(data) { //获取搜索子组件内容
				this.search = data;
			},
			closeFunc(data) { //获取子组件创建模板的状态
				this.close = data;
			},
		},
		created() {
			this.newTrData = this.tableData.trData.slice(0, 3)
		},
